@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
body {
  font-family: 'Open Sans', sans-serif;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }
  a:hover {
    text-decoration: none; }

p {
  margin: 0; }

.main {
  padding: 40px 0; }

.error {
  font-weight: bold;
  color: #ff0000;
  font-size: 18px;
  text-align: center;
  margin-top: 20px; }
  .error .big-text {
    font-size: 100px; }

.filmCardBlock {
  margin: 10px 0; }
  .filmCardBlock .filmCard {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px; }
    .filmCardBlock .filmCard img {
      max-width: 200px; }
    .filmCardBlock .filmCard .title {
      margin: 15px 0;
      font-weight: 800;
      text-align: center; }
    .filmCardBlock .filmCard .description {
      text-align: center;
      color: #818181;
      font-size: 12px; }
    .filmCardBlock .filmCard .genre, .filmCardBlock .filmCard .rating {
      display: flex;
      justify-content: center; }
      .filmCardBlock .filmCard .genre p, .filmCardBlock .filmCard .rating p {
        color: #818181;
        font-size: 12px;
        font-weight: 400; }
        .filmCardBlock .filmCard .genre p:first-child, .filmCardBlock .filmCard .rating p:first-child {
          font-weight: 800;
          color: #000;
          margin-right: 10px; }
    .filmCardBlock .filmCard .flex-block {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-top: 15px; }

.currentFilmBlock {
  padding: 40px 0; }
  .currentFilmBlock .title {
    font-weight: 800; }
  .currentFilmBlock .img-block img {
    width: 100%; }
  .currentFilmBlock .mr-block {
    margin: 20px 0; }
  .currentFilmBlock .flex-block {
    display: flex;
    justify-content: flex-start; }
    .currentFilmBlock .flex-block p:first-child {
      margin-right: 15px;
      font-weight: 800; }
    .currentFilmBlock .flex-block p span {
      margin-right: 5px; }
      .currentFilmBlock .flex-block p span:last-child {
        margin-right: 0; }
    .currentFilmBlock .flex-block .type-of-genre {
      display: flex;
      justify-content: flex-start;
      flex-flow: row wrap; }

